import 'bootstrap-loader';
import './css/style.less';
import 'font-awesome-webpack';
import $ from 'jquery';
import Vue from 'vue';
import VueRouter from 'vue-router';
import toon from './vue/toon.vue';

$(function() {
    Vue.use(VueRouter);
    const toon = { toon };
    const Bar = { template: '<div>bar bar</div>' };

    const routes = [
        { path: '/toon/:server/:name', component: toon },
        { path: '/bar', component: Bar }
    ];
    const router = new VueRouter({
        routes: routes // short for `routes: routes`
    });
    const app = new Vue({
        router: router,
        el: '#app',
        data: {
            pages: [
                {faIcon: "fa-dashboard", description: "Dashboard"},
                {faIcon: "fa-list", description: "List"},
                {faIcon: "fa-share-square-o", description: "Extract tool"},
                {faIcon: "fa-line-chart", description: "Budget"},
                {faIcon: "fa-tasks", description: "Jobs management"},
                {faIcon: "fa-cloud-upload", description: "Upload List"},
                {faIcon: "fa-superpowers", description: "Rock them all"}
            ]
        }
    });
});



